nav {
    display: flex;
	position: sticky;
	height: var(--header-height);
	top: 0;
	left: 0;
	right: 0;
    align-items: center;
    justify-content: center;
	background-color: var(--color-background);
}

nav .nav-container {
    width: 100%;
    max-width: var(--breakpoint-lg);
    display: flex;
    justify-content: space-between;
    align-items: center;
	padding: 0 var(--spacing-lg);
}

nav .nav-container .nav-logo {
    height: var(--logo-height);
}

nav .nav-container .nav-logo img {
    height: var(--logo-height);
}

nav .nav-container .menu-toggle {
	height: 20px;
	position: relative;
    display: none;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	-webkit-transition: .5s ease-in-out;
	-moz-transition: .5s ease-in-out;
	-o-transition: .5s ease-in-out;
	transition: .5s ease-in-out;
	cursor: pointer;
	width: 20px;
	margin-right: var(--spacing-lg);
}

nav .nav-container .menu-toggle:hover {
	color: #999;
}

nav .nav-container .menu-toggle span,
nav .nav-container .menu-toggle span:after,
nav .nav-container .menu-toggle span:before {
	display: block;
	position: absolute;
	height: 2px;
	width: 20px;
	right: 0;
	background: #999;
	border-radius: 2px;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	-webkit-transition: .25s ease-in-out;
	-moz-transition: .25s ease-in-out;
	-o-transition: .25s ease-in-out;
	transition: .25s ease-in-out;
}

nav .nav-container .menu-toggle span:after,
nav .nav-container .menu-toggle span:before {
	content: '';
}

nav .nav-container .menu-toggle span:after {
	top: -16px;
}

nav .nav-container .menu-toggle span:before {
	top: -8px;
}

nav .nav-container .menu-toggle span {
	top: 16px;
}

nav .nav-container .menu-toggle.open span:after {
	-webkit-transform: rotate(45deg);
	-moz-transform: rotate(45deg);
	-o-transform: rotate(45deg);
	transform: rotate(45deg);
	top: -13px;
}
nav .nav-container .menu-toggle.open span:before {
	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	transform: rotate(-45deg);
	top: -13px;
}

nav .nav-container .nav-items {
    display: flex;
    padding: 0;
}

nav .nav-container .nav-items .active .nav-items-icon,
nav .nav-container .nav-items .active a {
    color: black;
    font-weight: 500;
}

nav .nav-container .nav-items li {
	display: flex;
	color: var(--color-hover-text);
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 8px;
    list-style: none;
    margin: 0 var(--spacing-md);
	cursor: pointer;
}

nav .nav-container .nav-items li a {
	color: var(--color-item-text);
    text-decoration: none;
	font-weight: 400;
    transition: color var(--transition-duration) linear;
}

nav .nav-container .nav-items li .nav-items-icon {
	display: none;
	color: var(--color-item-text);
}

nav .nav-container .nav-social-icons {
    display: flex;
    padding: 0;
}

nav .nav-container .nav-social-icons li {
    list-style: none;
}

nav .nav-container .nav-social-icons li a {
    color: var(--color-nav);
    margin: 0 var(--spacing-unit);
    transition: color var(--transition-duration) linear;
}

nav .nav-container .nav-social-icons li a:hover {
    color: var(--color-hover-text);
}

@media screen and (max-width: 768px) {
	nav .nav-container {
		padding: 0;
	}

	nav .nav-container .nav-logo {
		margin-left: var(--spacing-lg);
	}

    nav .nav-container .nav-items {
        display: none;
    }

	nav .nav-container .nav-items.open {
		display: flex;
		justify-content: space-evenly;
		position: absolute;
		width: 100%;
		top: calc(var(--header-height) - 1px);
		background-color: var(--color-background);
		margin: 0;
		border-bottom: 1px solid var(--color-bg-footer);
		border-radius: 0 0 12px 12px;
		padding: 15px 0;
	}

	nav .nav-container .nav-items.open li .nav-items-icon {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		list-style: none;
		margin: 0 var(--spacing-md);
	}

    nav .nav-container .menu-toggle {
        display: block;
    }

    nav .nav-container .nav-social-icons {
        display: none;
    }

	.section-home {
		flex-direction: column-reverse;
	}
}

@media screen and (max-width: 480px) {
	nav .nav-container .nav-items.open {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
		grid-gap: 18px;
	}
}