.project-card {
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: var(--box-shadow-md);
    transition: transform 0.3s, box-shadow 0.3s;
    cursor: pointer;
}

.project-card:hover {
    box-shadow: var(--box-shadow-lg);
    transform: translateY(-5px);
}

.project-card .card-img {
    height: 200px;
    object-fit: cover;
}

.project-card .card-container {
    padding: var(--spacing-lg);
}

.project-card .card-container .card-title {
    font-family: var(--font-family-p);
    font-size: 22px;
    color: var(--color-header);
    margin: 0;
}

.project-card .card-container .card-description {
    font-size: 13px;
    line-height: 20px;
    font-family: var(--font-family-p);
    color: var(--color-hover-text);
}

.project-card .card-container .card-icons {
    display: flex;
    gap: 10px;
}

.project-card .card-container .card-icons .card-chip {
    border: 1px solid #ccc;
    border-radius: 12px;
    width: fit-content;
    margin: 10px 0;
}

.project-card .card-container .card-icons .card-chip p {
    margin: 0;
    padding: 6px 10px;
    font-family: var(--font-family-p);
    font-weight: 700;
    font-size: 12px;
    color: var(--color-header);
}

.project-card .card-container .card-links {
    display: flex;
    justify-content: right;
    align-items: center;
    gap: 15px;
}

.project-card .card-container .card-links a {
    color: var(--color-item-text);
    transition: color var(--transition-duration) linear
}

.project-card .card-container .card-links a:hover {
    color: var(--color-text);
}