section {
    display: flex;
    justify-content: center;
    align-items: center;
}

section .section-projects {
    display: flex;
    flex-direction: column;
    max-width: var(--breakpoint-lg);
    width: 100%;
    padding: 50px var(--spacing-lg);
    justify-content: center;
}

section .section-projects .section-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
    width: 100%;
}

section .section-projects .section-header .header-title {
    font-family: var(--font-family-header);
    font-size: var(--font-size-h4);
    color: var(--color-header);
    margin-top: 0;
    margin-bottom: var(--spacing-md);
}

section .section-projects .section-header .header-paragraph {
    font-family: var(--font-family-p);
    font-size: var(--font-size-md);
    color: var(--color-item-text);
    font-weight: 500;
    margin: 0;
}

section .section-projects .projects-container {
    display: grid;
    padding: 0 50px;
	grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
	grid-gap: 24px;
}

@media screen and (max-width: 769px) {
    section .section-projects .projects-container {
        padding: 0;
    }
}