section {
    display: flex;
    justify-content: center;
    align-items: center;
}

section .section-home {
    display: flex;
    height: calc(100vh - var(--header-height) - 100px);
    max-height: 890px;
    min-height: 500px;
    max-width: var(--breakpoint-lg);
    width: 100%;
    padding: 50px var(--spacing-lg);
    justify-content: center;
    align-items: center;
    gap: 50px;
}

section .section-home .home-description {
    width: 50%;
    padding-left: 40px;
}

section .section-home .home-description .header-name {
    font-size: var(--font-size-h4);
    font-family: var(--font-family-p);
    font-weight: 600;
    margin: 0;
    color: var(--color-header);
}

section .section-home .home-description .header-title {
    font-size: var(--font-size-h1);
    font-family: var(--font-family-header);
    margin: 0;
    color: rgb(13, 25, 92);
}

section .section-home .home-description .header-subtitle {
    font-size: var(--font-size-h3);
    font-family: var(--font-family-header);
    font-weight: 600;
    line-height: 30px;
    margin-top: 0;
    margin-bottom: 40px;
    color: var(--color-header);
}

section .section-home .home-description .header-description {
    font-size: var(--font-size-base);
    font-family: var(--font-family-p);
    color: var(--color-item-text);
    font-weight: 600;
    line-height: 25px;
    max-width: 400px;
    margin: 0;
}

section .section-home .home-photo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}

section .section-home .home-photo .my-photo {
    max-width: 400px;
}

@media screen and (max-width: 1024px) {
    section .section-home .home-description .header-name {
        font-size: 30px;
    }

    section .section-home .home-description .header-title {
        font-size: 70px;
    }

    section .section-home .home-description .header-subtitle {
        font-size: 40px;
    }

    section .section-home .home-description {
        width: 100%;
    }

    section .section-home .home-photo {
        width: 100%;
    }
}

@media screen and (max-width: 912px) {
    section .section-home {
        flex-direction: column-reverse;
    }
}

@media screen and (max-width: 769px) {
    section .section-home {
        padding: 50px var(--spacing-lg);
        height: calc(100vh - 160px);
        max-height: none;
    }

    section .section-home .home-description {
        width: 100%;
    }

    section .section-home .home-photo {
        width: 100%;
    }

    section .section-home .home-photo .my-photo {
        width: 80%;
    }
}

@media screen and (max-width: 480px) {
    section .section-home {
        max-width: none;
    }
    
    section .section-home .home-description {
        padding: 0;
    }

    section .section-home .home-description .header-name {
        font-size: var(--font-size-xl);
    }

    section .section-home .home-description .header-title {
        font-size: var(--font-size-h2);
    }

    section .section-home .home-description .header-subtitle {
        font-size: var(--font-size-h4);
        margin-bottom: 25px;
    }

    section .section-home .home-description .header-description {
        font-size: var(--font-size-md);
    }
}